import React, { Component } from 'react';
import Item from './item';
import '../../node_modules/bootstrap/dist/css/bootstrap.css';
import {Link} from 'react-router-dom';
class Items extends Component {
    state = {  }
 
    render() { 
        console.log('Items Called');
        return ( <div>
            <div className="container mt-5">
                
                   <div className="row">
                   {this.props.it.map((i)=>{
                return (<Item uniItem={i} key={i.id} onIncrement={this.props.onIncrement} onRemove={this.props.onRemove}/>);
                     })}
                   </div> 
            </div>
           <div className="justify-content-center d-flex mt-3 mb-3">
           <Link to="/login">  <button className="btn btn-primary btn-lg">Check-Out</button></Link>
           </div>
        </div> );
    }
}
 
export default Items;